html, body, .fullSize {
    width: 100%;
    height: 100%;
    margin: 0;
}

rect.selection {
    stroke          : gray;
    stroke-dasharray: 4px;
    fill            : transparent;
}

.selected {
    outline: gray dashed 1px;
    outline-offset: 5px;
}

#contextMenu {
    display: none;
}